/* Base styles for all content */
div {
    text-align: center;
  }
  
  .main {
    margin-bottom: 40px;
  }
  
  ol {
    text-align: left;
  }
  
  nav .nav-wrapper {
    background-color: #ffb8bd !important;
  }
  
  i {
    margin-right: 20px;
    color: black;
  }
  
  /* Dark Mode Styles */
  .dark-mode {
    background-color: rgb(102, 102, 102);
    color: white; /* font */
  }
  
  .dark-mode nav .nav-wrapper {
    background-color: #2c2c2c !important;
  }
  
  .dark-mode i {
    color: white;
    -webkit-transition: -webkit-filter 0.3s ease;
    transition: -webkit-filter 0.3s ease;
    -o-transition: filter 0.3s ease;
    transition: filter 0.3s ease;
    transition: filter 0.3s ease, -webkit-filter 0.3s ease; /* Smooth transition */

  }
  
  .dark-mode .brand-logo img {
    -webkit-filter: brightness(0) invert(1);
            filter: brightness(0) invert(1); /* Inverts the color of the logo */
    -webkit-transition: -webkit-filter 0.3s ease;
    transition: -webkit-filter 0.3s ease;
    -o-transition: filter 0.3s ease;
    transition: filter 0.3s ease;
    transition: filter 0.3s ease, -webkit-filter 0.3s ease; /* Smooth transition */
    width: 64px; /* Maintain the original logo size */
    height: auto;
  }
  
  /* Allow line breaks in MaterializeCSS tooltips */
  .material-tooltip {
    white-space: pre-line;
    max-width: 400px;
  }


